$white: #fff !default;
$gray-100: #f2f2f2 !default;
$gray-200: #e5e5e5 !default;
$gray-300: #dee2e6 !default;
$gray-400: #ced4da !default;
$gray-500: #9d9d9d !default;
$gray-600: #6c757d !default;
$gray-700: #495057 !default;
$gray-800: #343a40 !default;
$gray-900: #212121 !default;
$black: #000 !default;

$grays: () !default;
// stylelint-disable-next-line scss/dollar-variable-default
$grays: map-merge(
                (
                        "100": $gray-100,
                        "200": $gray-200,
                        "300": $gray-300,
                        "400": $gray-400,
                        "500": $gray-500,
                        "600": $gray-600,
                        "700": $gray-700,
                        "800": $gray-800,
                        "900": $gray-900
                ),
                $grays
);

$blue: #007bff !default;
$indigo: #6610f2 !default;
$purple: #6f42c1 !default;
$pink: #e83e8c !default;
$red: #dc3545 !default;
$orange: #fd7e14 !default;
$yellow: #ffc107 !default;
$green: #28a745 !default;
$teal: #20c997 !default;
$cyan: #17a2b8 !default;

$colors: () !default;
// stylelint-disable-next-line scss/dollar-variable-default
$colors: map-merge(
                (
                        "blue": $blue,
                        "indigo": $indigo,
                        "purple": $purple,
                        "pink": $pink,
                        "red": $red,
                        "orange": $orange,
                        "yellow": $yellow,
                        "green": $green,
                        "teal": $teal,
                        "cyan": $cyan,
                        "white": $white,
                        "gray": $gray-600,
                        "gray-dark": $gray-800
                ),
                $colors
);

$primary: #f26649;
$primary-dark: #c44d34;
$secondary: #637073;
$success: $green !default;
$info: $cyan !default;
$warning: $yellow !default;
$danger: $red !default;
$light: $gray-100 !default;
$dark: $gray-900;

$theme-colors: () !default;
// stylelint-disable-next-line scss/dollar-variable-default
$theme-colors: map-merge(
                (
                        "primary": $primary,
                        "primary-dark": $primary-dark,
                        "secondary": $secondary,
                        "success": $success,
                        "info": $info,
                        "warning": $warning,
                        "danger": $danger,
                        "light": $light,
                        "dark": $dark,
                        "white": #FFF
                ),
                $theme-colors
);

$c-text: $gray-900;
$c-text-light: #565656;
$c-border: $gray-200;
$c-page-background: $gray-100;
$c-page-background-dark: #637073;

// The yiq lightness value that determines when the lightness of color changes from "dark" to "light". Acceptable values are between 0 and 255.
$yiq-contrasted-threshold: 150;

$spacer: 1rem !default;
$spacers: () !default;
// stylelint-disable-next-line scss/dollar-variable-default
$spacers: map-merge(
                (
                        0: 0,
                        1: ($spacer * .25),
                        2: ($spacer * .5),
                        3: $spacer,
                        4: ($spacer * 2),
                        5: ($spacer * 3)
                ),
                $spacers
);

$body-bg: $white;
$body-color: $c-text-light;

$block-spacing: 70px;
$block-spacing-small: 30px;

$border-radius: 0;
$border-radius-lg: 0;
$border-radius-sm: 0;

// stylelint-disable value-keyword-case
$font-family-sans-serif: 'Roboto', sans-serif;
$font-family-base: $font-family-sans-serif;

$headings-margin-bottom: 1.5rem;

$headings-font-family: 'Lato', sans-serif;
$headings-font-weight: 900;
$headings-line-height: 1.3;

$font-size-base: 1rem;
$font-size-lg: $font-size-base;
$font-size-sm: ($font-size-base * .75) !default;

$line-height-base: 1.6;
$line-height-lg: $line-height-base;
$line-height-sm: $line-height-base;

$border-width: 1px !default;
$border-color: $gray-200;

$h1-font-size: $font-size-base * 2 !default;
$h2-font-size: $font-size-base * 1.5 !default;
$h3-font-size: $font-size-base * 1.285 !default;
$h4-font-size: $font-size-base * 1.125 !default;
$h5-font-size: $font-size-base * 1.1 !default;
$h6-font-size: $font-size-base !default;

$link-color: $primary;
$link-decoration: underline;

// Pagination

$pagination-padding-y: 14px;
$pagination-padding-x: 20px;
$pagination-padding-y-sm: .5rem;
$pagination-padding-x-sm: .75rem;
$pagination-padding-y-lg: 14px;
$pagination-padding-x-lg: 20px;
$pagination-line-height: 1.25;

$pagination-color: $white;
$pagination-bg: $c-text-light;
$pagination-border-color: $c-text-light;

$pagination-hover-color: $white;
$pagination-hover-bg: $c-text;
$pagination-hover-border-color: $c-text;

$pagination-active-color: $white;
$pagination-active-bg: $c-text;
$pagination-active-border-color: $c-text;

$pagination-disabled-color: $white;
$pagination-disabled-bg: $gray-500;
$pagination-disabled-border-color: $gray-300;

$btn-padding-y: 9px;
$btn-padding-x: 20px;
$btn-line-height: $line-height-base;

$btn-padding-y-sm: 4px;
$btn-padding-x-sm: 18px;
$btn-font-size-sm: 12px;
$btn-line-height-sm: 20px;

$btn-padding-y-lg: 1rem;
$btn-padding-x-lg: 2.5rem;
$btn-font-size-lg: 16px;
$btn-line-height-lg: 22px;

$input-btn-font-size-lg: 16px;
$input-color: $gray-700;
$input-border-color: $dark;
$input-border-width: 1px;
$custom-select-border-width: $input-border-width;
$custom-select-border-color: $input-border-color;
$custom-select-font-size-lg: $input-btn-font-size-lg;

$custom-select-line-height: $line-height-base;
$custom-select-padding-y: .5rem;
$custom-select-padding-x: 1rem;
$custom-select-height: 50px;
$custom-select-height-sm: 40px;
$custom-select-height-lg: 50px;

$navbar-nav-link-padding-x: 1rem;

// Tables
//
// Customizes the `.table` component with basic values, each used across all table variations.

$table-cell-padding: 15px;
$table-cell-padding-sm: 10px;

$table-bg: transparent !default;
$table-accent-bg: rgba($black, .05) !default;
$table-hover-bg: rgba($black, .075) !default;
$table-active-bg: $table-hover-bg !default;

$table-border-color: rgba(0, 0, 0, 0.1) !default;

$table-head-bg: #DDD;
$table-head-color: $c-text-light;

$table-striped-order: even;

// Cards

$card-spacer-y: .75rem !default;
$card-spacer-x: 1.25rem !default;
$card-border-width: 0;
$card-border-radius: 0;
$card-border-color: rgba($black, .125) !default;
$card-inner-border-radius: 0;
$card-cap-bg: $c-page-background;
$card-bg: $c-page-background;

$card-img-overlay-padding: 1.25rem !default;

//$card-group-margin:                 ($grid-gutter-width / 2) !default;
//$card-deck-margin:                  $card-group-margin !default;

$card-columns-count: 3 !default;
$card-columns-gap: 1.25rem !default;
$card-columns-margin: $card-spacer-y !default;

// Forms

$input-color: $c-text-light;
$input-box-shadow: none;

$input-height-border: $input-border-width * 2 !default;

$input-height: 50px;
$input-height-inner: $input-height - $input-height-border;

$input-height-sm: 40px;
$input-height-inner-sm: $input-height-sm - $input-height-border;

$input-height-lg: $input-height;
$input-height-inner-lg: $input-height-inner;

$input-transition: border-color .15s ease-in-out, box-shadow .15s ease-in-out !default;